import { Link } from "gatsby";
import React from "react";

const NavLink = ({
  path,
  link,
  title,
  className,
}: {
  path: string;
  link: string;
  title: string;
  className?: string;
}) => {
  const linkPath = `${link.toLowerCase().split(" ").join("-")}`;
  const isCurrent = path?.includes(linkPath);

  return (
    <li>
      <Link
        to={`/${linkPath}`}
        aria-label={`CodeCave ${title.toLowerCase()}`}
        itemProp="url"
        className={`text-lg font-semibold text-main-100 hover:text-main-10 ${
          isCurrent && "underline"
        } ${className}`}
      >
        <span itemProp="name">{title.toLowerCase()}</span>
      </Link>
    </li>
  );
};

export default NavLink;
